import { _decorator, Component, Node, Collider2D, Contact2DType, PhysicsSystem2D, EPhysics2DDrawFlags, IPhysics2DContact, instantiate, resources, Prefab, Layers, Vec3, NodePool, Vec2, ProgressBar } from 'cc';
import { BulletPerManeger } from './BulletPerManeger';
import { dijiTargetManeger } from './dijiTargetManeger';
import { livingSystemManeger } from './livingSystemManeger';
const { ccclass, property } = _decorator;

@ccclass('dijiDeathPerManeger')
export class dijiDeathPerManeger extends Component {
    public bulletArray = [];
    public hasEmeny:boolean = true;
    private targetArray:Array<Vec3> = null
    public bulletPool:NodePool;
    @property(livingSystemManeger)
    DijiDeathLivSysMag:livingSystemManeger = null;
    
    start() {
        this.initialDijiDeath();
        // let that = this;
        // this.bulletPool = new NodePool();
        // this.schedule(function(){
        //     this.hasEmeny = this.node.parent.getComponent(dijiTargetManeger).hasEmeny;
        //     if(this.hasEmeny){
        //         this.targetArray = this.node.parent.getComponent(dijiTargetManeger).emenyArray;
        //         // console.log('target number is ',this.targetArray.length)
        //         console.log('createBullet')
        //         that.createBullet();
        //         // if(that.bulletPool.size()>5){
        //         //     that.bulletPool.clear()
        //         // }
        //     }
        // },1)

        console.log('emeny information')
        let collider = this.node.getComponent(Collider2D);
        // console.log(collider);
        // console.log(collider);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
            collider.on(Contact2DType.PRE_SOLVE, this.onPreSolve, this);
            collider.on(Contact2DType.POST_SOLVE, this.onPostSolve, this);
        }
    }
    onLoad(){
        // director.enable = true;
        //绘制物理信息
        PhysicsSystem2D.instance.enable = true;
        PhysicsSystem2D.instance.debugDrawFlags = EPhysics2DDrawFlags.Aabb | 
            EPhysics2DDrawFlags.Pair | 
            EPhysics2DDrawFlags.CenterOfMass |
            EPhysics2DDrawFlags.Joint |
            EPhysics2DDrawFlags.Shape ;
    }

    update(deltaTime: number) {
        let node =this.node.parent;
        let y = node.position.y;
        let x = node.position.x;
        // if(x > -200){
        //     //敌军飞机不超过神木；
        // }else{
        x = x + 40 * deltaTime;
        // y = y - 10 * deltaTime;
        node.setPosition(x,y,0);
        // }
    }

    initialDijiDeath(){
        this.DijiDeathLivSysMag = new livingSystemManeger(100,10,10,12,100);
        // let that = this;
        // resources.load("sanshe/SpriteFrame",SpriteFrame,(err,SpriteFrame)=>{
        //     if (!SpriteFrame) {
        //         console.log('散射炮精灵图为空')
        //     }else{
        //         console.log('散射跑精灵图加载完成')
        //         this.node.getComponent(Sprite).spriteFrame = SpriteFrame;
        //         this.no
        //     }
        // })
        console.log("神木初始化成功")
    }

    onBeginContact (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体开始接触时被调用一次
        // this.hasEmeny = true;
        // this.targetArray.push(otherCollider.node.position)
        // console.log("maybe a information about position",otherCollider.node.position)
        // console.log('I am emeny onBeginContact');
        this.underFire(otherCollider);

    }
    onEndContact (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体结束接触时被调用一次
        // console.log('Work onEndContact');

    }
    onPreSolve (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 每次将要处理碰撞体接触逻辑时被调用
        // console.log('Work onPreSolve');
    }
    onPostSolve (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 每次处理完碰撞体接触逻辑时被调用
        // console.log('Work onPostSolve');

    }
    getAngle(start:Vec3,end:Vec3):number{
        let dx = start.x - (end.x );
        let dy = start.y - (end.y );
        let dir = new Vec2(dx,dy);

        let angle = dir.signAngle(new Vec2(1,0));

        let degreen = angle / Math.PI * 180;

        return -degreen
    }
    underFire(Collider: Collider2D){
        //判断是被谁攻击的

        //目前为默认减少10;
        if(Collider.node.name == "PaoBulletPer"){
            if(this.DijiDeathLivSysMag.livingValue - 40 <= 0){
                this.node.parent.destroy()
            }else{
                this.DijiDeathLivSysMag.livingValue -=40
                // console.log("生命值为：",this.SanShePaoLivSysMag.livingValue)
                this.node.parent.getChildByName("ProgressBar").getComponent(ProgressBar).progress = 0.5 * (this.DijiDeathLivSysMag.livingValue/100)
            }
        }

        if(Collider.node.name == "GuangZiBulletPer"){
            if(this.DijiDeathLivSysMag.livingValue - 80 <= 0){
                this.node.parent.destroy()
            }else{
                this.DijiDeathLivSysMag.livingValue -=80
                // console.log("生命值为：",this.SanShePaoLivSysMag.livingValue)
                this.node.parent.getChildByName("ProgressBar").getComponent(ProgressBar).progress = 0.5 * (this.DijiDeathLivSysMag.livingValue/100)
            }
        }

        if(Collider.node.name == "FengyiBulletPer"){
            if(this.DijiDeathLivSysMag.livingValue - 30 <= 0){
                this.node.parent.destroy()
            }else{
                this.DijiDeathLivSysMag.livingValue -=30
                // console.log("生命值为：",this.SanShePaoLivSysMag.livingValue)
                this.node.parent.getChildByName("ProgressBar").getComponent(ProgressBar).progress = 0.5 * (this.DijiDeathLivSysMag.livingValue/100)
            }
        }

        if(Collider.node.name == "shenmu"){
            this.node.destroy();
        }

        
    }
    createBullet(){
        // let node = new Node();
        // node.addComponent(Sprite);
        let that = this;
        resources.load("perfabs/Bullet_Pre",Prefab,(err,Prefab)=>{
            if(!Prefab){
                // console.log('子弹预制体为空')
            }else{
                // console.log('预制体制作完成')
                let node = instantiate(Prefab);
                if(!node){
                    // console.log('没有节点')
                }else{
                    // console.log(node)
                    // let pos = new Vec2();
                    node.layer = Layers.Enum.UI_2D;
                    let x = that.node.position.x + 400;
                    let y = that.node.position.x + 225;
                    
                    // let shit = pos.set(x,y)
                    // x = shit.x - 800;                   
                    // y = shit.y - 450;
                    // console.log(x,y)
                    node.setPosition(0,0,0);
                    that.node.addChild(node);
                    that.bulletArray.push(node);
                }
                
            }
        })
    }
}


